@import url("https://fonts.googleapis.com/css2?family=Fuzzy+Bubbles:wght@700&family=Roboto+Condensed:wght@300&display=swap");
html {
  width: 100%;
  min-width: 320px;
  padding: 0;
  margin: 0;
  background-color: rgb(20, 20, 20);
  background-position: center;
  background-attachment: fixed;
  font-family: "Roboto Condensed", sans-serif;
  overflow-x: hidden;
}
html body {
  overflow: hidden;
  margin: 0;
  padding: 0;
}

a:link {
  text-decoration: none;
}

a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

a:active {
  text-decoration: none;
}

::-webkit-scrollbar {
  width: 5px;
}

::-webkit-scrollbar-track {
  background: transparent;
}

::-webkit-scrollbar-thumb {
  background: rgba(255, 193, 79, 0.6);
  border-radius: 5px;
  opacity: 0.3;
}

nav {
  position: fixed;
  bottom: 0;
  width: 100%;
  min-width: 320px;
  background-color: rgba(55, 111, 121, 0.8);
  border-width: 5px;
  z-index: 1;
}
nav #nav-content {
  display: flex;
  justify-content: space-evenly;
}
nav #nav-content .icon {
  display: flex;
  justify-content: center;
  width: 100%;
  font-size: 25px;
  color: rgb(255, 193, 79);
}
nav #nav-content a, nav #nav-content #search {
  margin: 5px;
  padding: 5px 10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-color: rgb(17, 43, 48);
  border-width: 0;
  border-radius: 5px;
  font-family: "Roboto Condensed", sans-serif;
  font-size: 12px;
  text-align: center;
  color: rgb(243, 243, 243);
  cursor: pointer;
}

.homePage {
  margin: 0 auto 70px;
  width: 100%;
}
.homePage .header {
  font-size: 40px;
  font-family: "Fuzzy Bubbles", cursive;
  padding: 20px 0 10px;
  margin: 0 auto;
  width: 95%;
  color: rgb(255, 193, 79);
  text-align: center;
}
.homePage .homePage-text {
  font-size: 20px;
  margin: 0 auto 10px;
  width: 95%;
  color: rgb(243, 243, 243);
  text-align: center;
}

.genres-all {
  width: 100%;
  display: flex;
  flex-direction: column;
}
.genres-all .genre {
  padding-bottom: 10px;
}
.genres-all .genre .genre-name {
  width: 100%;
  font-size: 26px;
  text-align: center;
  margin: 10px 0 10px;
  padding: 10px 0;
  text-transform: capitalize;
  color: rgb(243, 243, 243);
  background-color: rgb(17, 43, 48);
  box-shadow: rgba(0, 0, 0, 0.4) 0px 2px 4px, rgba(0, 0, 0, 0.3) 0px 7px 13px -3px, rgba(0, 0, 0, 0.2) 0px -3px 0px inset;
}
.genres-all .genre .genre-ex {
  display: flex;
  justify-content: space-around;
  width: 95%;
  margin: 0 auto;
}
.genres-all .genre .genre-ex .genre-ex-movie {
  width: 100px;
  background-color: rgba(55, 111, 121, 0.8);
  border-radius: 5px;
  box-shadow: rgba(0, 0, 0, 0.4) 0px 2px 4px, rgba(0, 0, 0, 0.3) 0px 7px 13px -3px, rgba(0, 0, 0, 0.2) 0px -3px 0px inset;
}
.genres-all .genre .genre-ex .genre-ex-movie .genre-ex-movie-img img {
  width: 100%;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
}
.genres-all .genre .genre-ex h2 {
  font-size: 14px;
  padding: 5px;
  margin: 0;
  height: 30px;
  color: rgb(243, 243, 243);
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: wrap;
}
.genres-all .genre .genre-ex h3 {
  font-size: 12px;
  padding: 5px;
  margin: 0;
  color: rgb(255, 193, 79);
  text-transform: capitalize;
  float: right;
}
.genres-all .genre .genre-ex .showMore-btn {
  display: flex;
  flex-direction: column;
  align-self: center;
  align-items: center;
  padding: 10px 5px;
  background-color: rgba(55, 111, 121, 0.8);
  color: rgb(243, 243, 243);
  font-weight: bold;
  border-radius: 5px;
  border-width: 0;
  box-shadow: rgba(0, 0, 0, 0.4) 0px 2px 4px, rgba(0, 0, 0, 0.3) 0px 7px 13px -3px, rgba(0, 0, 0, 0.2) 0px -3px 0px inset;
  padding: 30px 5px;
}
.genres-all .genre .genre-ex .showMore-btn .showMore-btn-text {
  margin: 0;
  text-align: center;
  width: 50px;
}
.genres-all .genre .genre-ex .showMore-btn .icon {
  color: rgb(255, 193, 79);
  font-size: 25px;
}

.genre-allMovies {
  margin-bottom: 70px;
}
.genre-allMovies .header {
  font-size: 40px;
  font-family: "Fuzzy Bubbles", cursive;
  padding: 20px 0;
  margin: 0 auto;
  width: 95%;
  color: rgb(255, 193, 79);
  text-align: center;
  text-transform: capitalize;
}
.genre-allMovies #count {
  font-size: 20px;
  margin: 10px 0 10px;
  padding: 10px;
  text-transform: capitalize;
  color: rgb(243, 243, 243);
  background-color: rgb(17, 43, 48);
  box-shadow: rgba(0, 0, 0, 0.4) 0px 2px 4px, rgba(0, 0, 0, 0.3) 0px 7px 13px -3px, rgba(0, 0, 0, 0.2) 0px -3px 0px inset;
}
.genre-allMovies .allMovies {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  width: 95%;
  margin: 0 auto;
}
.genre-allMovies .allMovies .viewInfo-btn {
  width: 130px;
  background-color: rgba(55, 111, 121, 0.8);
  border-radius: 5px;
  box-shadow: rgba(0, 0, 0, 0.4) 0px 2px 4px, rgba(0, 0, 0, 0.3) 0px 7px 13px -3px, rgba(0, 0, 0, 0.2) 0px -3px 0px inset;
  margin: 10px 5px;
}
.genre-allMovies .allMovies .viewInfo-btn .movie-img img {
  width: 100%;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
}
.genre-allMovies .allMovies .viewInfo-btn h2 {
  font-size: 14px;
  padding: 5px;
  margin: 0;
  height: 30px;
  color: rgb(243, 243, 243);
  text-overflow: ellipsis;
  overflow: hidden;
}
.genre-allMovies .allMovies .viewInfo-btn h3 {
  font-size: 12px;
  padding: 5px;
  margin: 0;
  color: rgb(255, 193, 79);
  text-transform: capitalize;
  float: right;
}

.MovieInfo {
  margin-bottom: 70px;
  width: 100%;
  min-width: 320px;
}
.MovieInfo .MovieInfo-BackDrop {
  width: 100%;
  min-width: 320px;
  overflow: hidden;
}
.MovieInfo .MovieInfo-BackDrop #cover {
  width: 100%;
  -webkit-mask-image: linear-gradient(to bottom, rgb(0, 0, 0), rgba(0, 0, 0, 0));
          mask-image: linear-gradient(to bottom, rgb(0, 0, 0), rgba(0, 0, 0, 0));
}
.MovieInfo .MovieInfo-container {
  width: 95%;
  margin: 0;
  position: absolute;
  left: 50%;
  top: 150px;
  transform: translate(-50%);
}
.MovieInfo .MovieInfo-container .MovieInfo-title {
  width: 100%;
  display: flex;
  align-items: center;
}
.MovieInfo .MovieInfo-container .MovieInfo-title h1 {
  width: 100%;
  min-width: 280px;
  text-align: center;
  margin: 0 0 10px 0;
  border-radius: 5px;
  box-shadow: rgba(0, 0, 0, 0.4) 0px 2px 4px, rgba(0, 0, 0, 0.3) 0px 7px 13px -3px, rgba(0, 0, 0, 0.2) 0px -3px 0px inset;
  color: rgb(243, 243, 243);
  font-size: 34px;
  padding: 10px;
  background-color: rgba(17, 43, 48, 0.7);
}
.MovieInfo .MovieInfo-container .MovieInfo-Info {
  background-color: rgba(55, 111, 121, 0.7);
  padding: 10px;
  border-radius: 5px;
  box-shadow: rgba(0, 0, 0, 0.4) 0px 2px 4px, rgba(0, 0, 0, 0.3) 0px 7px 13px -3px, rgba(0, 0, 0, 0.2) 0px -3px 0px inset;
  margin-bottom: 70px;
  min-width: 280px;
}
.MovieInfo .MovieInfo-container .MovieInfo-Info #playOrSave {
  display: flex;
  justify-content: center;
}
.MovieInfo .MovieInfo-container .MovieInfo-Info #playOrSave #play, .MovieInfo .MovieInfo-container .MovieInfo-Info #playOrSave #save {
  margin: 0 auto 10px;
  cursor: pointer;
  padding: 5px auto;
  width: 100px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-color: rgb(17, 43, 48);
  border-width: 0;
  border-radius: 5px;
  font-family: "Roboto Condensed", sans-serif;
  font-size: 18px;
  text-align: center;
  color: rgb(243, 243, 243);
}
.MovieInfo .MovieInfo-container .MovieInfo-Info #playOrSave #play .icon, .MovieInfo .MovieInfo-container .MovieInfo-Info #playOrSave #save .icon {
  font-size: 50px;
}
.MovieInfo .MovieInfo-container .MovieInfo-Info #playOrSave #play p, .MovieInfo .MovieInfo-container .MovieInfo-Info #playOrSave #save p {
  width: 100%;
  margin: 0;
}
.MovieInfo .MovieInfo-container .MovieInfo-Info #date-time {
  margin: 5px;
  padding: 5px 10px;
  display: flex;
  justify-content: center;
  border-width: 0;
  border-radius: 5px;
  font-family: "Roboto Condensed", sans-serif;
  font-size: 18px;
  text-align: center;
  color: rgb(243, 243, 243);
}
.MovieInfo .MovieInfo-container .MovieInfo-Info #date-time p {
  margin: 5px;
}
.MovieInfo .MovieInfo-container .MovieInfo-Info .icon {
  display: flex;
  justify-content: center;
  width: 100%;
  font-size: 25px;
  color: rgb(255, 193, 79);
}
.MovieInfo .MovieInfo-container .MovieInfo-Info #description {
  font-size: 20px;
  color: rgb(243, 243, 243);
  margin: 0;
}
.MovieInfo .MovieInfo-container .movieInfo-credit {
  width: 100%;
  margin: 0 auto;
}
.MovieInfo .MovieInfo-container .movieInfo-credit ul {
  font-size: 22px;
  padding: 0;
  margin: 10px 0 0;
  color: rgb(243, 243, 243);
}
.MovieInfo .MovieInfo-container .movieInfo-credit ul li {
  list-style: none;
  font-size: 18px;
}
.MovieInfo .MovieInfo-container .movieInfo-credit ul li .dotIcon {
  color: rgb(255, 193, 79);
}

@media only screen and (min-width: 481px) {
  #nav-content {
    max-width: 480px;
    margin: 0 auto;
  }
  .genre-ex {
    max-width: 450px;
  }
  #count {
    text-align: center;
  }
  .MovieInfo {
    max-width: 100%;
  }
  .MovieInfo .MovieInfo-BackDrop {
    max-width: 550px;
    margin: 0 auto;
  }
  .MovieInfo .MovieInfo-container {
    max-width: 481px;
  }
}/*# sourceMappingURL=style.css.map */