// fonts importet from google.fonts
@import url('https://fonts.googleapis.com/css2?family=Fuzzy+Bubbles:wght@700&family=Roboto+Condensed:wght@300&display=swap');

// all variables

// fonts
$font-one: 'Fuzzy Bubbles', cursive;
$font-two: 'Roboto Condensed', sans-serif;

// background
@mixin background {    
    background-color: rgb(20, 20, 20);
    background-position: center;
    background-attachment: fixed;
}

// colors
$gray-one: rgb(34, 34, 34);
$gray-two: rgb(20, 20, 20);
$blue-one: rgb(17, 43, 48);
$blue-two: rgba(55, 111, 121, 0.8);
$background-blue-one: rgba(17, 43, 48, 0.7);
$background-blue-two: rgba(55, 111, 121, 0.7);
$font-color-white: rgb(243, 243, 243);
$font-color-yellow: rgb(255, 193, 79);
$color-yellow: rgba(255, 193, 79, 0.6);

// box-shadow
$box-shadow: rgba(0, 0, 0, 0.4) 0px 2px 4px, rgba(0, 0, 0, 0.3) 0px 7px 13px -3px, rgba(0, 0, 0, 0.2) 0px -3px 0px inset;

// overall styling
html {
    width: 100%;
    min-width: 320px;
    padding: 0;
    margin: 0;
    @include background;
    font-family: $font-two;
    overflow-x: hidden;

    body {
        overflow: hidden;
        margin: 0;
        padding: 0;
    }
}

a:link {text-decoration: none;}
a:visited {text-decoration: none;}
a:hover {text-decoration: none;}
a:active {text-decoration: none;}

::-webkit-scrollbar {
    width: 5px;
  }
::-webkit-scrollbar-track {
background:transparent;
}
::-webkit-scrollbar-thumb {
    background: $color-yellow;
    border-radius: 5px;
    opacity: 0.3;
}

// navigation styling
nav {
    position: fixed;
    bottom: 0;
    width: 100%;
    min-width: 320px;
    background-color: $blue-two;
    border-width: 5px;
    z-index: 1;

    #nav-content {
        display: flex;
        justify-content: space-evenly;

        .icon {
            display: flex;
            justify-content: center;
            width: 100%;
            font-size: 25px;
            color: $font-color-yellow;
        }
        
        a, #search {
            margin: 5px;
            padding: 5px 10px;
            display: flex;
            flex-direction: column;
            justify-content: center;
            background-color: $blue-one;
            border-width: 0;
            border-radius: 5px;
            font-family: $font-two;
            font-size: 12px;
            text-align: center;
            color: rgb(243, 243, 243);
            cursor: pointer;
        }
    }
}

// homepage styling
.homePage {
    margin: 0 auto 70px;
    width: 100%;

    .header {
        font-size: 40px;
        font-family: $font-one;
        padding: 20px 0 10px;
        margin: 0 auto;
        width: 95%;
        color: $font-color-yellow;
        text-align: center;
    }

    .homePage-text {
        font-size: 20px;
        margin: 0 auto 10px;
        width: 95%;
        color: $font-color-white;
        text-align: center;
    }
}

// styling the containers with all the different genres
.genres-all {
    width: 100%;
    display: flex;
    flex-direction: column;

    // individual genre container
    .genre {
        padding-bottom: 10px;

        .genre-name {
            width: 100%;
            font-size: 26px;
            text-align: center;
            margin: 10px 0 10px;
            padding: 10px 0;
            text-transform: capitalize;
            color: $font-color-white;
            background-color: $blue-one;
            box-shadow: $box-shadow;
        }

        // examples for movies of the specific genre
        .genre-ex {
            display: flex;
            justify-content: space-around;
            width: 95%;
            margin: 0 auto;

            .genre-ex-movie {
                width: 100px;
                background-color: $blue-two;
                border-radius: 5px;
                box-shadow: $box-shadow;

                .genre-ex-movie-img {
                    img {
                        width: 100%;
                        border-top-left-radius: 5px;
                        border-top-right-radius: 5px;
                    }

                }
            }

            h2 {
                font-size: 14px;
                padding: 5px;
                margin: 0;
                height: 30px;
                color: $font-color-white;
                text-overflow: ellipsis;
                overflow: hidden;
                white-space: wrap;
            }

            h3 {
                font-size: 12px;
                padding: 5px;
                margin: 0;
                color: $font-color-yellow;
                text-transform: capitalize;
                float: right;
            }

            // show all movies for the specific genre button
            .showMore-btn {
                display: flex;
                flex-direction: column;
                align-self: center;
                align-items: center;
                padding: 10px 5px;
                background-color: $blue-two;
                color: $font-color-white;
                font-weight: bold;
                border-radius: 5px;
                border-width: 0;
                box-shadow: $box-shadow;
                padding: 30px 5px;

                .showMore-btn-text {
                    margin: 0;
                    text-align: center;
                    width: 50px;
                }

                .icon {
                    color: $font-color-yellow;
                    font-size: 25px;
                }
            }
        }
    }
}

// All movies for specific genre styling
.genre-allMovies {
    margin-bottom: 70px;
    .header {
        font-size: 40px;
        font-family: $font-one;
        padding: 20px 0;
        margin: 0 auto;
        width: 95%;
        color: $font-color-yellow;
        text-align: center;
        text-transform: capitalize;
    }

    // movie count
    #count {
        font-size: 20px;
        margin: 10px 0 10px;
        padding: 10px;
        text-transform: capitalize;
        color: $font-color-white;
        background-color: $blue-one;
        box-shadow: $box-shadow;
    }

    .allMovies {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-evenly;
        width: 95%;
        margin: 0 auto;

        .viewInfo-btn {
            width: 130px;
            background-color: $blue-two;
            border-radius: 5px;
            box-shadow: $box-shadow;
            margin: 10px 5px;

            .movie-img {
                img {
                    width: 100%;
                    border-top-left-radius: 5px;
                    border-top-right-radius: 5px;
                }
            }

            h2 {
                font-size: 14px;
                padding: 5px;
                margin: 0;
                height: 30px;
                color: $font-color-white;
                text-overflow: ellipsis;
                overflow: hidden;
            }

            h3 {
                font-size: 12px;
                padding: 5px;
                margin: 0;
                color: $font-color-yellow;
                text-transform: capitalize;
                float: right;
            }
        }
    }
}

// movieinformation page styling
.MovieInfo {
    margin-bottom: 70px;
    width: 100%;
    min-width: 320px;

    // movie cover background
    .MovieInfo-BackDrop {
        width: 100%;
        min-width: 320px;
        overflow: hidden;

        #cover {
            width: 100%;
            // fade bottom of image
            -webkit-mask-image:-webkit-gradient(
                linear, left top, left bottom, 
                    from(rgba(0,0,0,1)), 
                    to(rgba(0,0,0,0)));
            mask-image: linear-gradient(to bottom, 
                rgba(0,0,0,1), 
                rgba(0,0,0,0)
            );
        }
    }

    // information about the chosen movie
    .MovieInfo-container {
        width: 95%;
        margin: 0;
        position: absolute;
        left: 50%;
        top: 150px;
        transform: translate(-50%);

        .MovieInfo-title {
            width: 100%;
            display: flex;
            align-items: center;

            h1 {
                width: 100%;
                min-width: 280px;
                text-align: center;
                margin: 0 0 10px 0;
                border-radius: 5px;
                box-shadow: $box-shadow;
                color: $font-color-white;
                font-size: 34px;
                padding: 10px;
                background-color: $background-blue-one;
            }
        }

        .MovieInfo-Info {
            background-color: $background-blue-two;
            padding: 10px;
            border-radius: 5px;
            box-shadow: $box-shadow;
            margin-bottom: 70px;
            min-width: 280px;

            #playOrSave {
                display: flex;
                justify-content: center;
                
                #play, #save {
                    margin: 0 auto 10px;
                    cursor: pointer;
                    padding: 5px auto;
                    width: 100px;
                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                    background-color: $blue-one;
                    border-width: 0;
                    border-radius: 5px;
                    font-family: $font-two;
                    font-size: 18px;
                    text-align: center;
                    color: rgb(243, 243, 243);
                    
                    .icon {
                        font-size: 50px;
                    }
                    
                    p {
                        width: 100%;
                        margin: 0;
                    }
                }
            }

            #date-time {
                margin: 5px;
                padding: 5px 10px;
                display: flex;
                justify-content: center;
                border-width: 0;
                border-radius: 5px;
                font-family: $font-two;
                font-size: 18px;
                text-align: center;
                color: $font-color-white;

                p {
                    margin: 5px;
                }
            }

            .icon {
                display: flex;
                justify-content: center;
                width: 100%;
                font-size: 25px;
                color: $font-color-yellow;
            }

            #description {
                font-size: 20px;
                color: $font-color-white;
                margin: 0;
            } 
        }

        // movie credit - directors and actors
        .movieInfo-credit {
            width: 100%;
            margin: 0 auto;

            ul {
                font-size: 22px;
                padding: 0;
                margin: 10px 0 0;
                color: $font-color-white;

                li {
                    list-style: none;
                    font-size: 18px;

                    .dotIcon {
                        color: $font-color-yellow;
                    }
                }
            }
        }
    }
}

// responsice design for 481px and larger
@media only screen and (min-width: 481px) {
    // navigation content
    #nav-content {
        max-width: 480px;
        margin: 0 auto;
    }

    // frontpage genre examples
    .genre-ex {
        max-width: 450px;
    }

    // frontpage genre count
    #count {
        text-align: center;
    }

    // movieinformation @media styling
    .MovieInfo {
        max-width: 100%;
    
        .MovieInfo-BackDrop {
            max-width: 550px;
            margin: 0 auto;
        }
    
        .MovieInfo-container {
            max-width: 481px;    
        }
    }
}
